:root{
    --hr-color: rgba(0, 0, 0, 0.09);
}

body{
    background-image: linear-gradient(to top, white, rgb(250, 250, 250));
}
main{
    padding-bottom: 90px;
}

video{
    width: 100vw;
    margin-bottom: 100px;
}

.blur-all{
    filter: blur(1.5px);
}

.images-container{
    display: flex;
    flex-flow: wrap;
    gap: 15px;
    justify-content: center;
}

.images-container img{
    box-shadow: 0 2px 10px 2px rgba(0, 0, 0, 0.297);
    border-radius: 8px;
    width: auto;
}

.images-container .aside-image-container{
    display: flex;
    flex-flow: column;
    gap: 15px;
}

#ad0{
    content: url('https://cache.dominos.com/wam/prod/market/BR/_pt/images/promo/b2c7ee9f-cdee-425d-a36a-23d69acc26e9.png');
}
#ad1{
    content: url('https://cache.dominos.com/wam/prod/market/BR/_pt/images/promo/bc20e5f5-35f6-4470-9660-cad68c93d2cc.png');
}
#ad2{
    content: url('https://cache.dominos.com/wam/prod/market/BR/_pt/images/promo/0b3866a0-cf73-4960-9688-6c753bcead10.png');
}
#ad3{
    content: url('https://cache.dominos.com/wam/prod/market/BR/_pt/images/promo/c10755b7-268f-46b0-94f0-dbff645e66e7.png');
}

@media(max-width: 800px)
{
    .images-container img{
        width: 95vw;
    }
    #ad0{
        content: url(../../../public/navigation-images/large-ad1.png);
    }
    #ad1{
        content: url(../../../public/navigation-images/large-ad2.png);
    }
    #ad2{
        content: url(../../../public/navigation-images/large-ad2.png);
    }
}