:root{
    --header-height: 60px;
}

header{
    height: var(--header-height);
    margin-bottom: 70px;
    position: fixed;
    top: 0;
    width: 100vw;
    z-index: 10;
    transition: background-color .5s;
}
.transparent-header{
    background-color: rgba(255, 255, 255, 0);
    /* box-shadow: 0 1px 10px rgba(0, 0, 0, 0.228); */
}
.solid-header{
    background-color: rgb(255, 255, 255);
    box-shadow: 0 1px 10px rgba(0, 0, 0, 0.228);
}

hr{
    position: absolute;
    margin: 5px 50px;
    height: 2px;
    width: 90vw;
    background-color: rgba(255, 255, 255, 0.2);
    border: none;
}

main div > h1 {
    text-align: center;
}

nav{
    height: var(--header-height);
    display: flex;
    justify-content: center;
    gap: 95px;
}

.main-nav > .logo-img{
    width: 50px;
    margin: 5px 5px;
}

.sub-nav {
    display: flex;
    flex-flow: row;
    gap: 95px;
}

.sub-nav .create-button{
    display: flex;
}

.create-button > button{
    width: 150px;
    background-color: rgba(255, 255, 255, 0);
    border: none;
    /* border: 1px solid rgb(223, 223, 223); */
    transition: transform .1s;
    transition: color 1s;
}
.white-letter{
    color: white;
}
.black-letter{
    color: black;
}

.create-button > button:hover {
    /* background-color: rgb(244, 244, 244); */
    transform: scale(1.08);
}

.sub-nav .create-button > button div{
    display: flex;
    justify-content: center;
}

.sub-nav .create-button > button p{
    font-size: 1em;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 400;
    width: 70px;
    margin: auto;
}

.cart-button button{
    width: 150px;
    background-color: rgb(255, 0, 0);
    border: 1px ridge rgba(0, 0, 0, 0.4);
    transition: .35s;
    height: var(--header-height);
    color: white;
    transition: all .1s;
}
.cart-button button:hover{
    background-color: rgb(194, 31, 31);
    transform: scale(1.03);
}
.sub-nav .cart-button button div{
    display: inline;
    background-color: rgb(123, 0, 2);
    border-radius: 150px;
    padding: 2px;
    margin-right: 3px;
    box-shadow: 0 1.5px 3px 1px rgb(0, 0, 0, 0.4);
}
.sub-nav .cart-button button p{
    display: inline;
}
.sub-nav .cart-button button div p{
    font-weight: 700;
}

.menu img{
    width: 60px;
}
.menu{
    display: none;
}

@media (max-width: 800px)
{
    header{
        height: auto;
    }
    
    nav{
        display: inline;
    }
    .display-none{
        display: none;
    }

    .main-nav > .logo-img{
        width: 50px;
    }

    .sub-nav{
        gap: 0;
        flex-flow: column;
        align-items: center;
    }
    .sub-nav .create-button{
        flex-flow: column;
        align-items: center;
    }
    .sub-nav button{
        border: none;
        color: black;
        background-color: white;
        width: 70vw;
    }
    .sub-nav button:hover{
        background-color: rgb(244, 244, 244);
    }
    .sub-nav button p{
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        font-weight: 500;
    }
    .sub-nav .create-button button{
        height: var(--header-height);
        text-align: center;
    }
    .sub-nav .create-button button p{
        margin: auto;
    }
    .sub-nav .cart-button button p{
        vertical-align: middle;
    }
    .sub-nav .cart-button button div p{
        color: white;
        font-size: 1em;
        margin: auto;
        font-family: 'Courier New', Courier, monospace, Geneva, Tahoma, sans-serif;
    }

    .main-nav {
        display: flex;
        justify-content: space-between;
    }

    .menu{
        display: inline;
    }
    .menu img{
        filter: invert(50%);
    }
}