body{
    overflow-y: overlay;
}

::-webkit-scrollbar {
    width: 5px;               /* width of the entire scrollbar */
}
  
::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0);        /* color of the tracking area */
}
::-webkit-scrollbar-track:hover{
    background: rgb(205, 205, 205);        /* color of the tracking area */
}
  
::-webkit-scrollbar-thumb {
    background-color: rgba(104, 104, 104, 0.5);    /* color of the scroll thumb */
    border-radius: 30px;       /* roundness of the scroll thumb */
}