.menu-title{
    font-family: 'Caveat', cursive;
    font-weight: 700;
    font-size: 1.5em;
    color: rgb(41, 41, 41);
}
.menu-title h1{
    text-align: start;
    margin-left: 90px;
}

.testing-class{
    background-color: rgb(255, 255, 255);
    color: black;
    padding: 2.5px 0;
    box-shadow: 0px 0px 1px 1px rgb(233, 233, 233);
}
.testing-class p{
    margin: 0;
}

.food-category{
    display: flex;
    justify-content: center;
    gap: 25px;
    flex-wrap: wrap;
    margin: 100px auto auto auto;
    max-width: 700px;
    min-width: 200px;
}

.food-container{
    box-shadow: 0 0 4px 1px rgba(0, 0, 0, .3);
    border-radius: 3px;
    min-width: 20vw;
    max-width: 30vw;
    transition: scale .1s;
    background-color: white;
}
.food-container:hover{
    background-color: rgb(243, 242, 242);
    scale: 1.05;
}
.food-container img{
    width: 180px;
    height: 180px;
    padding: 20px;
}
.food-container:hover img{
    transition: all .1s ease .03s;
    scale: 1.3;
}

@media(max-width: 800px){
    .food-container{
        min-width: 55vw;
    }
}